import CloudStorage from '../components/CloudStorage'
import '../styles/setting.scss'
import { Button } from 'antd'
import React from 'react'

function GrantAuthorizationButton () {
  return (
    <div className="auth">
      <div align="center">
        <p>Avi requires Google authorization to use custom cloud storage services.</p>
        <Button type="primary" ghost>Google Authorize</Button>
      </div>
    </div>
  )
}

export default class Setting extends React.Component {
  render () {
    return (
      <section className="setting">
        <div className="container">
          <CloudStorage></CloudStorage>
          <GrantAuthorizationButton></GrantAuthorizationButton>
        </div>
      </section>
    )
  }
}



